.globalContainer {
    height: auto;
    width: 100%;
    display: flex;
    color: var(--color-blanco);
    background-color: #F6D44C;
    position: relative;
}
.fondo {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    background-color: #F6D44C;
    z-index: -1;
    display: flex;
    justify-content: center;
    align-items: center;
}
.fondo img {
    object-fit: cover;
    width: 100%;
}
.contTexto {
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 100%;
    width: 60%;
    z-index: 2;
}

.textos {
    box-sizing: border-box;
    padding-left: 10%;
    visibility: visible;
    width: 100%;
    height: 150px;
    display: flex;
}
.textos h1 {
    font-size: 6vh;
}

.textos p {
    font-size: 4.7vh;
    width: 70%;
    margin-bottom: 1rem;
}

.inputs {
    display: flex;
    align-items: center;
    padding-left: 10%;
}

.inputs input {
    height: 60px;
    border-radius: 15px;
    border: none;
    margin-right: 1rem;
    font-size: 4vh;
    font-weight: bold;
    width: 30%;
    box-sizing: border-box;
    padding-left: 1.5rem;
    background-color: var(--color-azul);
    color: var(--color-blanco);
}

.inputs input::placeholder {
    display: flex;
    color: var(--color-blanco);
}

.botonHeader{
    width: 40%;
    border: none;
    height: 60px;
    border-radius: 15px;
    font-size: 4vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 20px;
    background-color: var(--color-azul);
    color: var(--color-blanco);
    font-weight: bold;
}
.flecha{
    width: 50px;
    height: 50px;
}
.botonHeader span{
    font-size:45px;
    font-weight: bold;
    color: var(--color-blanco);
    margin-right: 20%;
    transition: 0.5s all ease-in-out;
}

.botonHeader:hover span{
    transform: translateX(50px);
}
.celular {
    display: none;
}
.imagenes {
    display: none;
}
.paquetes {
    display: none;
}


@media only screen and (max-width: 800px){ 
    .globalContainer{
        background-color: #F6D44C;
        justify-content: center;
        align-items: center;
        width: 100%;
        min-height: auto;
        padding: 4rem 0 0 0;
    }
    .fondo {
        display: none;
    }
    .inputs{
        display: flex;
        flex-direction: column;
        width: 90%;
        justify-content: center;
        align-items: center;
        padding: 0px 0px;
    }
    .inputs input{
        width: 100%;
        margin-top: 10%;
        margin-right: 0;
    }
    .botonHeader{
        width: 100%;
        background-color: var(--color-azul);
        margin: 0;
        margin-top: 10%;
    }
    .contTexto{
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .textos {
        width: 100%;
        height: auto;
        background-color: yellowgreen;
    }
    .botonHeader span{
        font-size:45px;
        font-weight: bold;
        color: var(--color-blanco);
        margin-right: 10%;
        transition: 0.5s all ease-in-out;
    }
    .botonHeader:hover span{
        transform: translateX(20px);
    }
    .celular {
        height: 100vh;
        width: 100%;
        background-color: yellowgreen;
    }
    .imagenes {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        width: 100%;
    }
    .imagenes img {
        object-fit: cover;
        width: 90%;
    }
    .paquetes {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        width: 100%;
    }
    .paquetes img {
        object-fit: cover;
        width: 100%;
    }
}